body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-title {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.PokemonListItem {
  border: 1px solid black;
  padding: 20px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.PokemonListItem img {
  max-height: 100px;
}

.PokemonDetails {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.PokemonDetails img {
  max-height: 200px;
}

.PokemonDetails div {
  margin-top: 10px;
  font-weight: bold;
}

.pokemon-list-item {
  margin-bottom: 0.5rem;
  padding: 0.5rem;
  background-color: #f5f5f5;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.pokemon-list-item:hover {
  background-color: #e0e0e0;
}

.pokemon-list-item div {
  font-size: 1.2rem;
}

